* {
  box-sizing: border-box;
}

ul li {
  list-style-type: square;
  font-size: 16px;
}

.app-wrapper {
  margin: 0 auto;
  width: 1200px;
  display: grid;
  grid-template-rows: 60px 1fr;
  grid-template-columns: 2fr 10fr;
  grid-template-areas:
  "header header"
  "nav content"
  ;
}

.app-wrapper-content {
  grid-area: content ;
}




